<template>
  <div id="app">
    <database
      ref="database"
      :rowData="rowData"
      :columnData="columnData"
      :entry="entry"
      :doLayout="doLayout"

      @btnClick="btnClick"
      @selectChange="selectChange"
      @selectionChange="selectionChange"
    ></database>

    <el-button @click="vvv">验证</el-button>
    <el-button @click="vvv2">获取表格信息</el-button>

  </div>
</template>

<script>
import database from './database.vue'
export default {
  name: 'App',
  data(){
    return {  
      rowData: [
        {
          //普通框
          date: {
            type: 'normal',
            value: '2017-测试1',
            color: '#0ff'
          },
          //编辑框
          name: {
            type: 'edit',
            value: '王小虎',
            require: true,
          },
          //数字输入框
          number: {
            type: 'number',
            value: 12,
            min: 0,
            max: 10,
            floatLength: 2,
            is_percent: true,
            require: true,
          },
          //单选下拉(?q=)
          singleSelect: {
            type: 'single-select',
            url: './static/select.json',
            dataPos: 'data.result', //默认值
            id: 1,
            value: '选中',
            require: true,
          },
          //多选下拉
          mulSelect: {
            type: 'multiple-select',
            url: './static/select.json',
            // dataPos: 'data.result', //默认值
            id: [1,2], // 初始化时：id和value数组的值必须一一对应！
            value: ['小何','啊啊'],
            require: true,
          },
          //日期类型
          d: {
            type: 'date',
            value: '2012-03-22 12:53:53', //注意：datetimerange、daterange类型则必须是数组！
            format: 'yyyy-MM-dd', //组合：年：yyyy，月：MM，日：dd，时：HH，分：mm，秒：ss
            require: true,
          },
        },
        {
          //普通框
          date: {
            type: 'normal',
            value: '测试2',
            color: '#00f'
          },
          //编辑框
          name: {
            type: 'edit',
            value: 'mr wangwangwang',
            require: true,
          },
          //数字输入框
          number: {
            type: 'number',
            value: 6,
            min: 0,
            max: 10,
            floatLength: 2,
            is_percent: true,
            require: true,
          },
          //单选下拉(?q=)
          singleSelect: {
            type: 'single-select',
            url: './static/select.json',
            dataPos: 'data.result', //默认值
            id: 2,
            value: 'xxx',
            require: true,
          },
          //多选下拉
          mulSelect: {
            type: 'multiple-select',
            url: './static/select.json',
            // dataPos: 'data.result', //默认值
            id: [2], // 初始化时：id和value数组的值必须一一对应！
            value: ['啊啊'],
            require: true,
          },
          //日期类型
          d: {
            type: 'date',
            value: '2016-11-12 12:53:53', //注意：datetimerange、daterange类型则必须是数组！
            format: 'yyyy-MM-dd', //组合：年：yyyy，月：MM，日：dd，时：HH，分：mm，秒：ss
            require: true,
          },
        },
        //...
      ],
      columnData: [
        {
          title: '普通的',
          width: 240,
          props: 'date',
          // fixed: 'left',
        },
        {
          title: '姓名',
          width: 120,
          props: 'name',
          sortable: true,
        },
        {
          title: '数字输入',
          width: 140,
          props: 'number'
        },
        {
          title: '单选下拉',
          width: 140,
          props: 'singleSelect'
        },
        {
          title: '多选下拉',
          width: 180,
          props: 'mulSelect'
        },
        {
          title: '日期选择',
          props: 'd'
        },
        /**
        * @name 操作列
        * @description #只能有一个操作列#
        * @version 1.0
        */
        {
          title: '操作',
          btnName: [{ style: 'success', icon: 'el-icon-upload2'}, {name: '删除', style: 'danger', icon: 'el-icon-delete'}],

          width: 150,
          fixed: 'right'
        },
      ],
      entry: {
        //普通框
        date: {
          type: 'normal',
          value: 0
        },
        //编辑框
        name: {
          type: 'edit',
          value: '',
          require: true,
        },
        //数字输入框
        number: {
          type: 'number',
          value: '',
          min: 0,
          // max: 10,
          floatLength: 2,
          require: true,
        },
        //单选下拉(?q=)
        singleSelect: {
          type: 'single-select',
          url: './static/select.json',
          dataPos: 'data.result', //默认值
          id: '',
          value: '',
          require: true,
        },
        //多选下拉
        mulSelect: {
          type: 'multiple-select',
          url: './static/select.json',
          // dataPos: 'data.result', //默认值
          id: function(){ // 初始化时：id和value数组的值必须一一对应！
            return []
          }, 
          value: function(){ // 初始化时：id和value数组的值必须一一对应！
            return []
          }, 
          require: true,
        },
        //日期类型
        d: {
          type: 'date',
          value: function(){
            return '2012-01-21';
          },
          require: true,
        },
      },
      doLayout: 0,
      
    }
  },
  created(){
  },
  mounted(){
    window.addEventListener('resize', ()=>{
      this.doLayout = Date.now()
    });
  },
  methods: {
    btnClick(e){
      console.log(e)
    },
    selectChange(e){
      console.log(e)
    },
    selectionChange(e){
      console.log(e)
    },
    //验证
    vvv(){
      let msg = this.$refs.database.validate();
    },
    vvv2(){
      let msg = this.$refs.database.getTableData();
      console.log(msg)
    },

    
  },
  components: { database }
}
</script>

<style>
  
</style>



