<template>
  <el-table
    border
    ref="elTable"
    :data="row"
    :row-key="row=>row._rowid"
    :row-class-name="tableRowClassName"
    :height="height"
    :max-height="maxHeight"
    size="mini"
    style="width: 100%"
    @selection-change="val => {$emit('selectionChange', val)}">

    <!-- 选择框 -->
    <el-table-column
      type="selection"
      fixed="left"
      width="40">
    </el-table-column>

    <!-- 状态列 -->
    <el-table-column
      fixed="left"
      :render-header="renderStatus"
      :width="36">
      <template slot-scope="scope">
        <span
          style=
          "
            position: relative;
            display: flex;
            align-items: center;
            justify-content: space-between;
          ">
          <template v-if="scope.row._highlight == true">
            <i
              style="color: #f00; font-size: 14px;"
              class="el-icon-circle-close-outline">
            </i>
          </template>
          <template v-else-if="scope.row._add == true">
            <i
              style="color: #bb6dff; position: relative; font-size: 14px;"
              class="el-icon-upload2">
            </i>
          </template>
          <template v-else-if="scope.row._edit == true">
            <i
              style="color: #e38c09; position: relative; font-size: 12px;"
              class="el-icon-edit-outline">
            </i>
          </template>
          <template v-else>
            <i
              style="color: #0fd50f; font-size: 14px;"
              class="el-icon-circle-check-outline">
            </i>
          </template>

        </span>
      </template>
    </el-table-column>

    <!-- 按钮操作列 -->
    <template v-if="btnCol != false">
      <el-table-column
        :label="btnCol.title"
        :fixed="btnCol.fixed? btnCol.fixed: false"
        :width="btnCol.width? btnCol.width: 150">
        <template slot-scope="scope">
          <span style="display: inline-block; margin-right: 4px;" v-for="(btnName, btnIndex) in btnCol.btnName" :key="btnIndex">
            <el-tooltip v-if="btnName.name" effect="dark" :content="btnName.name" placement="top">
              <el-button 
                plain
                :type="btnName.style || 'info'" 
                round 
                :key="btnIndex" 
                size="mini" 
                :icon="btnName.icon"
                @click="$emit('btnClick', {btn: btnName, row: scope.row})">
              </el-button>
            </el-tooltip>
            <el-button 
              v-else
              plain
              :type="btnName.style || 'info'" 
              round 
              :key="btnIndex" 
              size="mini" 
              :icon="btnName.icon"
              @click="$emit('btnClick', {btn: btnName, row: scope.row})">
            </el-button>
          </span>
        </template>
      </el-table-column>
    </template>

    <el-table-column
      v-for="(columnItem, colIndex) in column.filter(coll => !coll.btnName)"
      :key="columnItem._colid + colIndex"
      :label="columnItem.title"
      :width="columnItem.width || 'auto'"
      :min-width="colIndex == column.length - 1? 200: 90"
      :sortable="columnItem.sortable || false"
      :sort-method="(a, b)=>{return sortFn(a, b, columnItem.props)}"
      :fixed="columnItem.fixed || false">
      <template slot-scope="scope">
        <!-- 普通 -->
        <div v-if="scope.row[columnItem.props].type === 'normal'">
          <span :style="{color: scope.row[columnItem.props].color || ''}">{{ scope.row[columnItem.props].value }}</span>
        </div>

        <!-- 编辑框 -->
        <div 
          style="position: relative;" 
          v-if="scope.row[columnItem.props].type === 'edit'"
          @mouseleave="editMouseLeave(scope.row._rowid, columnItem._colid)"
          @mouseenter="$set(ixc, 'ixc-'+scope.row._rowid+'-'+columnItem._colid, true)" >
          <span 
            class="edit-span" 
            v-show="ixc['ixc-'+scope.row._rowid+'-'+columnItem._colid] != true"
            >
            {{scope.row[columnItem.props].value}}
          </span>
          <el-input
            type="textarea"
            size="mini"
            :autosize="{maxRow: 80}"
            :style="{
              position: ixc['ixc-'+scope.row._rowid+'-'+columnItem._colid] == true? 'static': 'absolute', 
              'z-index': ixc['ixc-'+scope.row._rowid+'-'+columnItem._colid] == true? 99: -1, 
            }"
            resize="none"
            @focus="
              $set(isfocus, 'isfocus-'+scope.row._rowid+'-'+columnItem._colid, true); 
              $set(ixc, 'ixc-'+scope.row._rowid+'-'+columnItem._colid, true);
            "
            @blur="
              editBlur(
                $event, 
                scope.row._rowid, 
                columnItem._colid,
                columnItem.props
              )
              $set(ixc, 'ixc-'+scope.row._rowid+'-'+columnItem._colid, false)
              $set(isfocus, 'isfocus-'+scope.row._rowid+'-'+columnItem._colid, false)
            "
            :value="scope.row[columnItem.props].value">
          </el-input>
          <!-- 图标 -->
          <i 
          v-if="
            scope.row[columnItem.props].require == true
            ? 
            scope.row[columnItem.props].value == undefined || scope.row[columnItem.props].value == null || scope.row[columnItem.props].value.trim() == ''
            : 
            false
          " 
          class="edit-icon el-icon-question" style="color: #ff8495;"></i>
          <i v-else class="edit-icon el-icon-edit-outline"></i>
        </div>

        <!-- 数字输入框 -->
        <div 
          style="position: relative; overflow: hidden" 
          v-if="scope.row[columnItem.props].type === 'number'" 
          @mouseleave="editMouseLeave(scope.row._rowid, columnItem._colid)"
          @mouseenter="$set(ixc, 'ixc-'+scope.row._rowid+'-'+columnItem._colid, true)" >
          <span 
            class="edit-span" 
            v-show="ixc['ixc-'+scope.row._rowid+'-'+columnItem._colid] != true"
            >
            {{scope.row[columnItem.props].value | toPercent(scope.row[columnItem.props].is_percent)}}
          </span>
          <el-input
            :style="{
              position: ixc['ixc-'+scope.row._rowid+'-'+columnItem._colid] == true? 'static': 'absolute', 
              'z-index': ixc['ixc-'+scope.row._rowid+'-'+columnItem._colid] == true? 99: -1, 
            }"
            size="mini"
            type="number"
            @focus="
              $set(isfocus, 'isfocus-'+scope.row._rowid+'-'+columnItem._colid, true); 
              $set(ixc, 'ixc-'+scope.row._rowid+'-'+columnItem._colid, true);
            "
            @blur="
              validNumber(
                $event,
                scope.row, 
                columnItem._colid,
                columnItem.props
              );
              $set(ixc, 'ixc-'+scope.row._rowid+'-'+columnItem._colid, false)
              $set(isfocus, 'isfocus-'+scope.row._rowid+'-'+columnItem._colid, false)
            "
            v-model="number['number-'+scope.row._rowid+'-'+columnItem._colid]">
          </el-input>
          <i v-if="scope.row[columnItem.props].require == true? isNaN2(scope.row[columnItem.props].value): false" class="edit-icon el-icon-question" style="color: #ff8495;">
          </i>
          <template v-else>
            <i v-show="!ixc['ixc-'+scope.row._rowid+'-'+columnItem._colid]" class="edit-icon el-icon-d-caret"></i>
          </template>
        </div>

        <!-- 单选下拉 -->
        <div style="position: relative;" v-if="scope.row[columnItem.props].type === 'single-select'">
          <el-select
            size="mini"
            clearable
            placeholder=""
            @clear="selectClear(
              scope.row._rowid, 
              columnItem._colid,
              columnItem.props
            )"
            @focus="e => { selectFocus(e, scope.row, columnItem) }"
            @change="id => { 
              selectChange(
                id,
                scope.row._rowid, 
                columnItem._colid,
                columnItem.props
              );
              $emit('selectChange', {col: columnItem, id: id, row: scope.row})
            }"
            :value="scope.row[columnItem.props].value"
            filterable
            remote
            :remote-method="query => remoteMethod(
              query, 
              scope.row, 
              columnItem
            )"
            :loading="loading['loading-' + scope.row._rowid + '-' + columnItem._colid]">
            <el-option
              v-for="item in option['option-' + scope.row._rowid + '-' + columnItem._colid]"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
          <i v-if="scope.row[columnItem.props].require == true? isUnexpect(scope.row[columnItem.props].value): false" class="edit-icon el-icon-question" style="color: #ff8495;">
          </i>
          <i v-else style="right: -6px;" class="edit-icon el-icon-arrow-down"></i>
        </div>
        
        <!-- 多选下拉 -->
        <div v-if="scope.row[columnItem.props].type === 'multiple-select'">
          <el-select
            size="mini"
            multiple
            placeholder=""
            @focus="e => { selectFocus(e, scope.row, columnItem) }"
            v-model="mutiple['mutiple-' + scope.row._rowid + '-' + columnItem._colid]"
            @change="ids => multipleChange(ids, scope.row._rowid, columnItem._colid, columnItem.props)"
            remote
            :remote-method="query => remoteMethod(
              query, 
              scope.row, 
              columnItem
            )"
            :loading="loading['loading-' + scope.row._rowid + '-' + columnItem._colid]">
            <el-option
              v-for="item in option['option-' + scope.row._rowid + '-' + columnItem._colid]"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
          <i v-if="scope.row[columnItem.props].require == true? isUnexpect(scope.row[columnItem.props].value): false" class="edit-icon el-icon-question" style="color: #ff8495; right: 21px; font-size: 12px;">
          </i>
        </div>

        <!-- 日期|时间类型 -->
        <div
          style="position: relative;" 
          v-if="
            scope.row[columnItem.props].type === 'year' ||
            scope.row[columnItem.props].type === 'month' ||
            scope.row[columnItem.props].type === 'date' ||
            scope.row[columnItem.props].type === 'datetime' ||
            scope.row[columnItem.props].type === 'datetimerange' ||
            scope.row[columnItem.props].type === 'daterange'
          ">
          <el-date-picker
            v-model="scope.row[columnItem.props].value"
            placeholder=""
            align="center"
            size="mini"
            :type="scope.row[columnItem.props].type"
            :editable="false"
            prefix-icon="none"
            @change="val=>{ dateChange(val, scope.row._rowid, columnItem._colid, columnItem.props) }"
            :value-format="scope.row[columnItem.props].format || ''"
            :format="scope.row[columnItem.props].format || ''">
          </el-date-picker>
          <i v-if="scope.row[columnItem.props].require == true? isUnexpect(scope.row[columnItem.props].value): false" class="edit-icon el-icon-question" style="color: #ff8495; right: 0px;">
          </i>
          <i v-else style="right: -4px;" class="edit-icon el-icon-date"></i>
        </div>

      </template>
    </el-table-column>

  </el-table>
</template>

<script>
  import './database.css' 
  let database = {
    props: {
      rowData: {},
      columnData: {},
      entry: {
        default: false
      },

      height: {},
      maxHeight: {},
      doLayout: {},

    },
    data() {
      return {
        row: [],
        column: [],
        //存放下拉loading
        loading: {},
        //存放下拉框
        option: {},
        //存放【多选下拉的v-model】
        mutiple: {},
        //存放【数字编辑框的v-model】
        number: {},
        //存放列的ixc状态
        ixc: {},
        //输入框是否是focus状态
        isfocus: {},
        //按钮操作列
        btnCol: false,

      }
    },
    watch: {
      //对表格重新布局
      doLayout(){
        this.doLayoutFn();
      },
    },
    created(){
      this.init();
    },
    mounted(){
      
    },
    methods: {
      console(v){
        console.log(v)
      },
      isNaN2(val){
        if(val === '' || (val + "").trim() === ''){
          return true ;
        }
        return isNaN(val);
      },
      isUnexpect(val){
        if(val == null || val == undefined || (val+'').trim() == ''){
          return true;
        }
        return false;
      },
      /*
      * 初始化
      */
      init(){
        //初始化：row 行
        this.row = this.clone(this.rowData);
        for(let i=0; i<this.row.length; i++){
          this.row[i]._rowid = i;
          this.row[i]._edit = false;
          this.row[i]._highlight = false;
        };

        //初始化：column 列
        this.column = this.clone(this.columnData);
        this.btnCol = this.column.filter(c=>c.btnName && c.btnName.length != 0)[0]; //按钮操作列
        
        for(let i=0; i<this.column.length; i++){
          this.column[i]._colid = i;
        };

        /**
         * 初始化：其他模型
         */
        for(let i=0; i<this.row.length; i++){
          for(let [key, value] of Object.entries(this.row[i])){
            //多选框-模型
            if(value.type == 'multiple-select'){
              for(let j=0; j<this.column.length; j++){
                if(this.column[j].props == key){
                  this.$set(
                    this.mutiple, 
                    'mutiple-'+this.row[i]._rowid+'-'+this.column[j]._colid,
                    //因为这时还没有option，用value代替展示！
                    value.value
                  )
                };
              }
            }
            //数字输入框-模型
            if(value.type == 'number'){
              for(let j=0; j<this.column.length; j++){
                if(this.column[j].props == key){
                  this.$set(
                    this.number, 
                    'number-'+this.row[i]._rowid+'-'+this.column[j]._colid,
                    value.value
                  )
                };
              }
            }

          };
        };

      },
      //表格行的状态类
      tableRowClassName({row, rowIndex}) {
        if (row._edit == true && row._highlight != true) {
          return 'edit-row';
        }
        if(row._highlight == true){
          return 'highlight-row';
        }
        return '';
      },
      //自定义-排序
      sortFn(a, b, props) {
        a = a[props].value;
        b = b[props].value;
        if(a == b) return 0;
        if(a*1 == a && b*1 == b){
            return a - b < 0? -1: 1;
        };
        if(typeof a == typeof b) return a < b? -1: 1;
        return typeof a < typeof b? -1: 1;
      },
      //渲染状态列的表头
      renderStatus(h, { column, $index }){
        let iconStyle = {
          display: 'block',
          fontSize: '18px',
          color: '#24c876',
          textAlign: 'center',
          cursor: 'pointer',
          outline: 'none',
          border: 'none',
        }
        let lastIndex = this.row.length;
        let _this = this;
        function click() {
          let entry_info = _this.deepClone(_this.entry);
          //新增行的特征
          entry_info._rowid = Date.now();
          entry_info._add = true;
          entry_info._edit = true;
          entry_info._highlight = false;

          for(let [key, value] of Object.entries(entry_info)){
            //value字段是function
            if(typeof value.value == 'function'){
              entry_info[key].value = value.value();
            }
            //id字段是function
            if(typeof value.id == 'function'){
              entry_info[key].id = value.id();
            }
          };
          _this.row.push(entry_info);
          _this.$nextTick(()=>{
            //滚动到新加行
            let table = _this.$refs.elTable.$el;
            let wrap = table.querySelector('.el-table__body-wrapper');
            let lastTr = table.querySelector('.el-table__row:nth-last-of-type(1)');
            if(wrap && lastTr){
              wrap.scrollTop = lastTr.offsetTop;
            };
          })
        }
        if (JSON.stringify(this.entry) == "{}" || this.entry == false) {
          iconStyle['fontSize'] = '13px';
          iconStyle['color'] = '#17d8ee';
          return (
            <i
              style = {
                iconStyle
              }
              class = "el-icon-info" >
            </i>
          )
        }else{
          return (
            <el-tooltip
              effect = "dark"
              content = "增加一行"
              placement = "top" >
              <i
                on-click = { click }
                style = { iconStyle }
                class = "el-icon-circle-plus" >
              </i>
            </el-tooltip>
          )
        };
      },

      //编辑框-失去焦点时触发
      editBlur(e, _rowid, _colid, props){
        this.modifyByRowCol(_rowid, _colid, props, e.target.value);
      },
      //数字编辑框-验证
      validNumber(e, row, _colid, props){
        for(let i=0; i<this.row.length; i++){
          if(this.row[i]._rowid == row._rowid){
            let v = e.target.value;
            if((v+"").trim() == '' || isNaN(v)){
              this.$set(this.number, 'number-'+row._rowid+'-'+_colid, v)
              this.row[i][props].value = v;
              this.row[i]._edit = true;
              this.row[i]._highlight = false;
              return ;
            }
            if(this.row[i][props].min != undefined && v<this.row[i][props].min){
              v = this.row[i][props].min;
            }
            if(this.row[i][props].max != undefined && v>this.row[i][props].max){
              v = this.row[i][props].max;
            }
            if(this.row[i][props].floatLength != undefined){
              v = (v*1).toFixed(this.row[i][props].floatLength);
            }
            this.$set(this.number, 'number-'+row._rowid+'-'+_colid, v)
            this.row[i][props].value = v;
            this.row[i]._edit = true;
            this.row[i]._highlight = false;
            return ;
          }
        }
      },
      //输入框的离开事件
      editMouseLeave(_rowid, _colid){
        if(this.isfocus['isfocus-'+_rowid+'-'+_colid] == true){
          return ;
        }
        this.$set(this.ixc, 'ixc-'+_rowid+'-'+_colid, false);
      },
      //
      

      //根据_rowid和_colid修改value或id（在this.row的引用上修改）
      modifyByRowCol(_rowid, _colid, props, value, id){
        for(let i=0; i<this.row.length; i++){
          if(this.row[i]._rowid == _rowid){
            this.row[i][props].value = value;
            // 状态为_edit为true
            this.row[i]._edit = true;
            this.row[i]._highlight = false;
            if(id != undefined){
              this.row[i][props].id = id;
            };
            break ;
          }
        }
      },
      //下拉框-下拉请求
      remoteMethod(query, row, col){
        query = (query? query + "": '').trim();
        this.$set(this.loading, 'loading-' + row._rowid + '-' + col._colid, true);
        this.$http.get(row[col.props].url+'?q='+query).then(res => {
          this.$set(this.loading, 'loading-' + row._rowid + '-' + col._colid, false);
          //准备请求结果的位置
          let dataPos = 'data.result'; //默认位置
          let real_res = res.data;
          if(row[col.props].dataPos){
            dataPos = row[col.props].dataPos;
          };
          let dataPosArr = dataPos.split('.');
          for(let i=0; i<dataPosArr.length; i++){
            if(real_res[dataPosArr[i]] == undefined){
              console.warn('dataPos中有不存在的字段！请检查dataPos字段或者请求是否成功！')
              return ;
            };
            real_res = real_res[dataPosArr[i]];
          };
          //把id转化为字符串！
          real_res = real_res.map(item => {
            item.id = item.id + '';
            return item;
          });
          //更新option
          this.$set(this.option, 'option-'+row._rowid+'-'+col._colid, real_res);
          //【多选框】得到option后，交换value和id
          if(row[col.props].type == 'multiple-select') {
            this.$nextTick(()=>{
              for(let i=0; i<this.row.length; i++){
                for(let j=0; j<this.column.length; j++){
                  if(this.column[j]._colid == col._colid && this.row[i]._rowid == row._rowid){
                    this.$set(
                      this.mutiple, 
                      'mutiple-'+this.row[i]._rowid+'-'+this.column[j]._colid,
                      //因为这时还没有option，用value代替展示！
                      row[col.props].id.map(item=>{
                        item = item + '';
                        return item;
                      })
                    );
                    break ;
                  }
                }
              };
            });
          };
        }).catch(err=>{
          this.$set(this.loading, 'loading-' + row._rowid + '-' + col._colid, false);
          console.error(err);
        })
      },
      //单选下拉-清除
      selectClear(_rowid, _colid, props){
        this.modifyByRowCol(_rowid, _colid, props, '', '');
      },
      //下拉-聚焦
      selectFocus(e, row, col){
        this.remoteMethod(' ', row, col)
      },
      //单选下拉-change事件
      selectChange(id, _rowid, _colid, props){
        let cur_opt = this.option['option-'+_rowid+'-'+_colid];
        if(!cur_opt) return ;
        for(let i=0; i<cur_opt.length; i++){
          if(cur_opt[i].id == id){
            this.modifyByRowCol(_rowid, _colid, props, cur_opt[i].name, id);
            break;
          }
        };
      },
      //多选下拉-change事件
      multipleChange(ids, _rowid, _colid, props){
        let opt = this.option['option-' + _rowid + '-' + _colid];
        //再没有下拉框时，就清除 #一个多选下拉#，这时ids是展示值数组，需要按 #顺序# 对齐！
        if(opt == undefined){
          for(let i=0; i<this.row.length; i++){
            if(this.row[i]._rowid == _rowid){
              //寻找被清除多选下拉的序号
              let mark = this.row[i][props].value.map(() => false);
              for(let j=0; j<this.row[i][props].value.length; j++){
                for(let k=0; k<ids.length; k++){
                  if(this.row[i][props].value[j] == ids[k]){
                    mark[j] = true;
                    continue ;
                  }
                };
              };
              //找到为false的序号
              for(let m=0; m<mark.length; m++){
                if(mark[m] == false){
                  this.row[i][props].value.splice(m, 1);
                  this.row[i][props].id.splice(m, 1);
                  this.row[i]._edit = true;
                  this.row[i]._highlight = false;
                  break ;
                }
              }
              break ;
            }
          }
          return ;
        };

        //有下拉框时
        let nameArr = [];
        for(let i=0; i<ids.length; i++){
          for(let j=0; j<opt.length; j++){
            if(ids[i] == opt[j].id){
              nameArr.push(opt[j].name);
            };
          };
        };
        this.modifyByRowCol(_rowid, _colid, props, nameArr, ids);
      },
      //日期：date类型-change事件
      dateChange(d, _rowid, _colid, props){
        this.modifyByRowCol(_rowid, _colid, props, d);
        //日期的需要强制渲染（否则第一次修改界面不跟新）
        this.$forceUpdate()
      },
      //对表格重新布局
      doLayoutFn(){
        this.$refs.elTable.doLayout();
      },
      //对表格的require进行验证
      validate(){
        for(let i=0; i<this.row.length; i++){
          for(let j=0; j<this.column.length; j++){
            if(!this.column[j].props){
              continue ;
            }else{
              //验证条件
              if(this.row[i][this.column[j].props].require == true){
                if(
                  this.row[i][this.column[j].props].value == '' ||
                  this.row[i][this.column[j].props].value == undefined ||
                  this.row[i][this.column[j].props].value == null ||
                  (this.row[i][this.column[j].props].value + '').trim() == ''
                ){
                  this.row[i]._highlight = true;
                  this.$message.warning(`第${i*1+1}行，${this.column[j].title}：必填！`)
                  this.$nextTick(()=>{
                    //强制刷新状态
                    this.$forceUpdate()
                  });
                  return false;
                }
              }
            };
          };
        };
        return true;
      },
      //得到表格数据信息
      getTableData(){
        return {column: this.column, row: column};
      },
      //拷贝对象
      clone(obj){
        return JSON.parse(JSON.stringify(obj))
      },
      /**
       * @name 深拷贝
       */
      deepClone(data) {
        let t = type(data), o, i, ni;
        if(t === 'array') {
          o = [];
        }else if( t === 'object') {
          o = {};
        }else {
          return data;
        }
        if(t === 'array') {
          for (i = 0, ni = data.length; i < ni; i++) {
            o.push(this.deepClone(data[i]));
          }
          return o;
        }else if( t === 'object') {
          for( i in data) {
            o[i] = this.deepClone(data[i]);
          }
          return o;
        }
        function type(obj) {
          let toString = Object.prototype.toString;
          let map = {
            '[object Boolean]'  : 'boolean', 
            '[object Number]'   : 'number', 
            '[object String]'   : 'string', 
            '[object Function]' : 'function', 
            '[object Array]'    : 'array', 
            '[object Date]'     : 'date', 
            '[object RegExp]'   : 'regExp', 
            '[object Undefined]': 'undefined',
            '[object Null]'     : 'null', 
            '[object Object]'   : 'object'
          };
          return map[toString.call(obj)];
        }
      },

      
    },
    filters: {
      toPercent: function(value, isPercent) {
        if (isPercent == true) {
          if (!value) return ''
          return Number.parseFloat(value * 100).toFixed(2) + '%';
        } else {
          return value;
        };
      },
    },

  }
  export default database;
</script>

<style scoped>
@media only print {
  .fa-font,
  .fa-angle-down,
  .fa-sort,
  .el-icon-date {
    display: none;
  }
}
.edit-icon{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0px;
  font-size: 10px;
  color: #bcbcbc;
}
.el-icon-question.edit-icon{
  font-size: 12px;
  color: #ff8495;
}
.edit-span {
  width: 3.5%;
  height: 28px;
  line-height: 1.5;
  padding-right: 12px;
  display: table-cell;
  vertical-align: middle;
  position: relative;
}

</style>



